#contact {
  color: #333333;
  font-size: 16px;
  padding-top: 5em;
}

#contact h2, #contact h3 {
  font-weight: normal;
}

.decorate {
  background: #f2f3f5;
  margin: 0 6em;
  padding: 40px 30px;
  box-shadow: 0 5px 14px rgba(0, 0, 0, 0.1);
  border: 1px solid #ededed;
  line-height: 2.2em;
}

.decorate ul h2 {
  border-bottom: 2px solid #333;
}

#contact a {
  color: #333;
}

#contact a:hover {
  color: #28a9e2;
}


@media screen and (max-width: 768px) {

  .grid-col-2 {
    grid-template-columns: 1fr;
    grid-auto-columns: column;
    grid-gap: 0;
  }

  .decorate {
    margin: 1em;
    margin-bottom: 0;
    padding: 0;
    padding: 1em; 
  }

  #contact {
    font-size: 15px;
  }

}