#about {
  padding-top: 8em;
}

.float {
    float: left;
    margin: 1em 3em;
  }
  
.aboutUs {  
  margin-top: 2em;
  padding: 0;  
  width: 100%;  
  height: auto;
}

.aboutUs .container {
  padding: 0 0 0 0;
}

.grid-col-2 {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-gap: 2em;
}

.message-box {
  padding: 0 2.5em;
}

.message-box h3 {
  margin: 0;
  color: var(--secondary-color);
  font-size: 2.5em;
}

.message-box p {
  font-size: 1em;
  margin: 1em;
}

.message-image {
  position: relative;
}

.message-image img {
  width: 550px;
  height: auto;
  z-index: 1;  
}

.message-image .black {
  background: #000;
  height: 90%;
  width: 500px;
  position: absolute;  
  left: 12%;
  top: 12%;
  border-radius: 50px 50px 0px 50px;
  z-index: -1;
}

@media only screen and (max-width: 794px) {
  .message-box {
    padding: 0 1em;
  }

  .message-box h3 {
    font-size: 1.8em;
  }

  .message-image img{
    width: 100%;
    height: auto;
  }

  .message-image .black {
    width: 100%;
    left: 5%;
  }

}
