.navbar {
  height: 100vh;
  background: rgba(0, 0, 0, 1) url(../../images/dept.jpg) no-repeat fixed center 0px / cover;
  width: 100%;
  z-index: 2;
}

.navbar .overlay {
  background: #00000096;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
}


.header { 
  /*box-shadow: 1px 1px 4px 0 rgba(0,0,0,.1);*/
  position: fixed;
  width: 100%;
  z-index: 11;
  background: #333333;
}

.header ul {
  margin: 0;
  padding: 0;
  list-style: none;
  overflow: hidden;
  background-color: #fff;
}

.header li a {
  display: block;
  padding: 0.8em 0;
  padding-left: 3em;
  text-decoration: none;
  color: #fff;
  cursor: pointer;
}


.header .logo {
  display: flex;
  justify-content: center;
  align-items: cente;
  flex-direction: column;
  float: left;
  font-size: 0.8em;
  padding: 1em 0;
  padding-left: 3em;
  text-decoration: none;  
}

.header .logo h3{
  line-height: 1em;
  text-align: center;
  color: red;
  font-size: 1.4em;  
}

.header .menu-icon {
  cursor: pointer;
  float: right;
  padding: 3em 2em;
  position: relative;
  user-select: none;
}

.header .menu {
  clear: both;
  max-height: 0;
  transition: max-height .2s ease-out;
  background: #333;
  filter: brightness(100%);

}


.header .menu-icon .nav-icon {
  background: #fff;
  display: block;
  height: 4px;
  position: relative;
  transition: background .2s ease-out;
  width: 33px;
}

.header .menu-icon .nav-icon:before,
.header .menu-icon .nav-icon:after {
  background: #fff;
  content: '';
  display: block;
  height: 100%;
  position: absolute;
  transition: all .2s ease-out;
  width: 100%;
}

.header .menu-icon .nav-icon:before {
  top: 10px;
}


.header .menu-icon .nav-icon:after {
  top: -10px;
}


.header .menu-btn {
  display: none;
}

.header .menu-btn:checked ~ .menu {
  max-height: 25em;
  overflow-y: scroll;
}

.header .menu-btn:checked ~ .menu-icon .nav-icon {
  background: transparent;
}

.header .menu-btn:checked ~ .menu-icon .nav-icon:before {
  transform: rotate(-45deg);
  top:0;
}

.header .menu-btn:checked ~ .menu-icon .nav-icon:after {
  transform: rotate(45deg);
  top:0;
}

/* Active */

.header .active {
  background-color: #fff;
  color: #28a9e2;
  border-radius: 100px 100px 0px 100px;
  transition: all 1s ease-out;
}


/* Banner */
.banner-container {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  color: #fff;
  width: 100%;
  text-align: center;
}

.heading {
  font-size: 1.5em;
  vertical-align: middle;
  position: relative;
  overflow: hidden;
  width: 100%;
}

.sub-heading {
  font-size: 1.2em;
  font-weight: 400;
}

.cd-words-wrapper b {
  text-transform: uppercase;
}

@media screen and (min-width: 1015px) {
  .header {
    background: transparent;
    transition: background 0.3s ease-in-out;
  }

  .header.header-scrolled {
    background: #fff;
    color:  #28a9e2;
    /*box-shadow: 1px 5px 12px #1f1f1f;*/
    box-shadow: 0 0 .5em rgba(0, 0, 0, .5);
  }

  .header.header-scrolled .active {
    background: var(--blue-color);
    color: #fff;
  }
  

  .header.header-scrolled a {
    color: #28a9e2;
  }

  .header li {
    float: left;
  }
  .header li a {
    padding: 0.6em 0.8em;
    color: #fff; 
    cursor: pointer;   
  }
  .header .menu {
    clear: none;
    float: right;
    max-height: none;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    padding: 2em;
    margin-right: 3em;
    background: transparent;
  }
  .header .menu-icon {
    display: none;
  }

  .header .logo {
    margin-left: 6em;
    margin-top: 1em;
  }

  /* Heading and Subheading */
  .heading {
    font-size: 2em;
  }

  .sub-heading {
    font-size: 1.6em;
  }

}
