
#cells {
  padding-top: 5em;
}

.section-title {
  margin-bottom: 4rem;
}

.section-title p {
  font-size: 18px;
}

.section-title h3{
  color: #000;
  font-size: 38px;
  font-weight: 500;
  margin-top: 3rem; 
  margin-bottom: 10px;
  padding: 0;;
}

.text-center {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;  
}

.cells-section{
  background: #f2f3f5;
  height: 100%;
  width: 100%;
  margin-top: 4rem;
}

#cells .main-section {
  margin: 30px;
  padding: 0 70px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.cells-container {  
  padding: 0 20px;
  width: 32%;
}

.services-inner-box{
  padding: 15px;
  background: #ffffff;
  margin-bottom: 30px;
  border-radius: 50px 50px 0px 50px;
  position: relative;
  transition: all 0.3s ease-in-out 0s;
}
.ser-icon{
  width: 90px;
  height: 90px;
  text-align: center;
  line-height: 90px;
  position: relative;
  z-index: 1;
}
.ser-icon i{
  font-size: 50px;
  color: #333333;
}
.services-inner-box h2{
  color: #000;
  font-size: 22px;
  font-weight: 600;
  position: relative;
  z-index: 1;
  margin-bottom: 10px;
}
.services-inner-box p{
  font-size: 14px;
  position: relative;
  margin: 1px;
  z-index: 1;
}

.services-inner-box a {
  color: #000;
}

.services-inner-box .date{
  display: inline-block;
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 14px;
  background: #28a9e2;
  color: #fff;
  padding: 8px 10px;
  text-align: center;
  border-radius: 50px 50px 0px 50px;
  transition: 0.3s ease-in-out 0s;
}

.services-inner-box .date i {
  font-size: 20px;
}

.services-inner-box .date a {
  color: #fff;
}

.services-inner-box::after{
  background: #28a9e2;
  width: 0px;
  height: 0px;
  content: "";
  position: absolute;
  right: 0px;
  bottom: 0px;
  border-radius: 50px 50px 0px 50px;
  transition: all 0.3s ease-in-out 0s;
  z-index: 0;
}
.services-inner-box:hover::after{
  width: 100%;
  height: 100%;
  transition: all 0.3s ease-in-out 0s;
}

.services-inner-box:hover .ser-icon i{
  color: #ffffff;
}

.services-inner-box:hover h2{
  color: #ffffff;
  transition: all 0.3s ease-in-out 0s;
}
.services-inner-box:hover p{
  color: #ffffff;
}

.services-inner-box:hover .date{
  background: #ffffff;
  z-index: 1000;
  font-size: 14px;
  border-radius: 0 50%;
}

.services-inner-box:hover .date a{
  color: #28a9e2;
}


@media only screen and (max-width: 1012px) {

  #cells .main-section {
    flex-direction: column;
    padding: 0;    
  }

  .cells-container, .news-container, .post-box, .our-team {
    width: 100%;
    padding: 0;
  }
}
