

#faculty {
  padding-top: 5em;
}

#faculty .main-section {
  display: flex;
  width: 100%;
  height: auto;
  flex-wrap: wrap;
}

.our-team {
  text-align: center;
  margin: 1em 1em;
  position: relative;
  height: 100%;
  
}

.our-team:last-child {
  border-bottom: none;
}

.our-team .pic{
  border-top: none;  
  border-bottom: 5px solid #28a9e2;
  border-radius: 50% 50% 0 50%;
  overflow: hidden;
  transition: all 0.5s ease 0s;
  height: 100%;
}

.our-team .pic img{
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  margin-bottom: -8px;
  transition: all 0.5s ease 0s;
}

.our-team .team-content .date{
  display: inline-block;
  position: absolute;
  font-size: 14px;
  background: #28a9e2;
  color: #fff;
  right: 0;  
  padding: 7px 10px;
  text-align: center;
  border-radius: 50% 50% 0 50%;
}
.our-team .date a {
  color: #fff;
}
.our-team .date i {
  font-size: 18px;
}
.our-team:hover .pic{    
  border-bottom-color: #1c142b;
  border-radius: 0 50%;
}

.our-team .team-content { 
  padding: 2em 0; 
}

.our-team .title{
  font-size: 22px;
  font-weight: 700;
  color: #28a9e2;
  text-transform: uppercase;
  margin: 0 0 5px 0;
}
.our-team .post{
  display: inline-block;
  font-size: 17px;
  color: #878484;
  text-transform: capitalize;
  margin-bottom: 5px;
}
.our-team:hover .date {
  background: #1c142b;
  color: #fff;
  border-radius: 0 50%;
  transition: all 0.5s ease 0s;
}




@media screen and (max-width: 600px) {
  .our-team {
    padding: 0 1em;
    padding-bottom: 1em;
    border-bottom: 3px #28a9e2 solid;
  }

  #faculty .section-title h3{
    font-size: 28px;
  }
}