#news {
  background: #f2f3f5;
  width: 100%;  
  margin-top: -2em; 
  padding-top: 5em;
}

.news-buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;  
}

.news-buttons button {
  font-size: 1.2em;
  border: none;
  border-radius: 100px 100px 0px 100px;
  transition: all 1s ease-out;
  padding: 1em 2em;
  background-color: #fff;
  color: #28a9e2; 
  margin: 1em 0.5em;
  cursor: pointer;
  font-weight: 500;
}

.news-buttons .active {
  background-color: #28a9e2;
  color: #fff;
  box-shadow: 2px 20px 45px 5px rgba(0,0,0,.2);
}

#news .main-section {
  height: 120vh;
  display: flex;  
  flex-direction: row;
  flex-wrap: wrap;
  overflow-y: scroll;   
  margin: 1em 0;
  padding: 0 3em;
}

#news a:hover {
  color: #28a9e2;
}

.news-container {
  position: relative;
  overflow: hidden;
  border-radius: 100px 100px 0px 100px;
  -webkit-box-shadow: 0 0 10px #ccc;
  box-shadow: 0 0 10px #ccc;
  width: 28%;
  height: 20em;    
  margin: 1.5em;  
}

.news-container .img-overlay {
  border-radius: 100px 100px 0px 100px;
  position: absolute;
  top: 10px;
  left: 10px;
  right: 10px;
  bottom: 10px;
  background: rgba(40, 169, 226, 0.80);  
  color: #35424C;
  -webkit-transition: all .5s;
  transition: all .5s;
  z-index: 0;
  -webkit-transform: translateY(10px);
  transform: translateY(10px);
  opacity: 1;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}

.news-container .img-overlay .text{
  position: absolute;
  right: 20%;
  width: 60%;
}

.news-container .img-overlay .text h1{
  font-size: 1.5rem;
  font-weight: bold;
  border-bottom: 1px solid #fff;
  padding: 0;
  text-align: center;
  position: sticky;
  display: block;
  background: #fff;
}

.news-container .img-overlay .text p{
  font-size: 1.1rem;
  margin: 0;
  padding: 1em 0;
  line-height: 1em;
  width: 100%;
}

.news-container .img-overlay .ext-link{
  display: inline-block;
  position: absolute;
  bottom: 5px;
  right: 5px;
  font-size: 24px;
  color: #000;
  padding: 0 5px;
  text-align: center;
  transition: 0.3s ease-in-out 0s;
}

.news-container .img-overlay .ext-link a{ 
  color: #000;
}
