@media screen and (max-width: 1012px) {
  #news .main-section {
    justify-content: center;
    align-items: center;  
    margin: 0;
    height: 80vh;
    
  }
  .news-container {
    width: 50%;
    margin: 0 1em;   
  }
  
}

@media screen and (max-width: 550px) {
  .news-container {
    width: 100%;  
  }
  .news-buttons button{
    margin: 0.5em 0.25em;
    font-size: 1.2em;
    padding: 0.5em 1em;
  }  
}