#academics {
  background: #f2f3f5;
  width: 100%;
  padding-bottom: 3em;
  padding-top: 5em;  
}

#academics .main-section {
  display: flex;
  justify-content: space-between;
  align-items: center; 
}


#academics .section-title {
  padding-top: 3em;
}

#academics .main-section.grid-col-3 {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}

.post-box .date{
  display: inline-block;
  position: absolute;
  bottom: 5px;
  right: 5px;
  background: #28a9e2;
  color: #fff;
  padding: 10px 15px;
  text-align: center;
}

.post-thumb{
  position: relative;
  border-radius: 100px 100px 0px 100px;
  overflow: hidden;
  height: 230px;
}

.post-thumb img {
  height: 230px;
  width: 100%;
}

.post-box{
  -webkit-box-shadow: 0px 5px 35px 0px rgba(148, 146, 245, 0.15);
  box-shadow: 0px 5px 35px 0px rgba(148, 146, 245, 0.15);
  background: #ffffff;
  border-radius: 100px 100px 0px 0px;  
  margin: 1em;
  width: auto;
}

.post-box:hover .post-thumb::before{
  opacity: .6;
  -webkit-transform: translateY(0);
  transform: translateY(0);
}


.post-box .post-thumb::before{
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: rgba(51, 51, 51, 0.80);
  opacity: 0;
  -webkit-transition: all .5s;
  transition: all .5s;
  -webkit-transform: translateY(10px);
  transform: translateY(10px);
}

.post-info{
  padding: 0px 15px 40px 15px;
  background: #ffffff;
  text-align: left;
  margin-top: 40px;
}

.post-info p{
  margin: 0px;
  font-size: 15px;
}

.post-info h4{
  font-size: 18px;
  font-weight: 700;
  color: #35424c;
}

.post-info ul{
  display: block;
  text-align: left;
  margin-bottom: 15px;
}
.post-info ul li{
  display: inline-block;
  text-align: center;
}

@media screen and (max-width: 1012px) {

  #academics .main-section {
    margin: 0;
    padding: 0;
  }

  .post-box {
    margin: 1em 0.5em;        
  }

  #academics .main-section.grid-col-3 {
    grid-template-columns: repeat(2, 24em);
    grid-gap: 1em;
    
  }
}

@media screen and (max-width: 786px) {

  #academics .main-section {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;    
  }

  #academics .main-section.grid-col-3 {
    grid-template-columns: repeat(1, 22em);
  }

  .post-box {
    margin: 1em;
    
  }

  .post-info{
    padding: 0 1em;
  }
}